<template>
<v-container style="height:100%; position:relative">
  <Appbar :text="'Neue Kategorie'"/>

<!-- without solo -->
  <v-text-field  
    color="gray" v-model="newCategoryName" 
    label="Name deiner Kategorie" required clearable autofocus/>

  <v-card
    max-width="100%"
    tile
  >    
   <!-- <v-virtual-scroll :items="items" :item-height="50"
      height="300">   -->
      <v-list v-show="words.length > 0 ? true : false" style="max-height:70vh;overflow-y:auto;">
 <v-row v-for="item of words" :key="item" class="mx-1 my-1"> 
   <v-col class="col-9 px-0 py-0">
     <v-list-item >
      <v-list-item-content> 
          {{item}}          
      </v-list-item-content>
    </v-list-item>

    </v-col>
    
    <v-col class="col-3 centercenter text-center px-1 py-1" style="text-align: center; center">
         <v-btn icon small fab ><v-icon small @click="delWort(item)">mdi-close</v-icon></v-btn>
    </v-col>
    </v-row>
    <!-- </v-virtual-scroll> -->
</v-list>

    <v-dialog
      v-model="dialog"
      persistent
      max-width="600px"
    >
    
      <v-card>
        <v-card-title>
          <!-- <span class="headline">New Word</span> -->
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field 
                color="grey"
                isactive= true
                v-model="newWord"
                  label="Neues Wort"
                  required
                  autofocus
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <!-- <small>*indicates required field</small> -->
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="grey darken-1"
            text
            fab
            @click="dialog = false"
          >
                              <v-icon>mdi-close</v-icon>

            
          </v-btn>
          <v-btn
            color="gray darken-1"
            text
            fab
            @click="saveWord()"
          >
                    <v-icon>mdi-check</v-icon>

            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


 

     <!-- <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              New Item
            </v-btn> -->


            <!-- ***** -->




  </v-card>
<!-- 
    v-bind="attrs"
          v-on="on" -->
  <!-- <template v-slot:activator="{ on, attrs }"> -->
        <v-btn class="mt-5 btnBottom"
        fab
        x-large
          style="right:3vw"

          color="gray"
          dark
        @click="dialog = true"
        >
          <v-icon small >mdi-plus</v-icon>
        </v-btn>

        <v-btn  class="mt-2 btnBottom" 
          fab
          color="grey"
          dark
          @click="finished()"
        >
          <v-icon small >mdi-check</v-icon>
        </v-btn>
      <!-- </template> -->

      </v-container>
</template>
<script>
import axios from 'axios';
// "Test1", "Test2", "Test3" 
import Appbar from '@/components/Appbar.vue';
  export default {
    data() {
      return {
        words: [ ],
        dialog: false,
        newWord: "",
        newCategoryName: ""
      }
    },
    components: {
      Appbar
    },
    methods: {
      saveWord() {
        // console.log("saved");
        let found = this.words.find(elm => elm == this.newWord);
        if(this.newWord != "" && found == null){
        this.words.push(this.newWord);
        // history.replaceState({},"","http://localhost:8080/");
        }
        this.newWord = "";
      },
      async finished(){
        console.log("sd");
        // await axios.post("http://localhost:3000/categories",{ name: this.newCategoryName, words: this.words})


        let c = { name: this.newCategoryName, words: this.words};
        // await axios.post("http://localhost:3000/categories",c);


        await axios.post("/categories",c);

        // await axios.post("http://localhost:3000/categories",{ name: this.newCategoryName, words: this.words})
        history.go(-1);
      },
      delWort(item){
        let ind = this.words.indexOf(item);
        this.words.splice(ind,1);
      }
    },
  }
</script>

<style lang="scss" scoped>
.centercenter{
 justify-content:center; align-items:center; display: flex
}
.btnBottom{
position:absolute; 
bottom: 1.5vh;
}
</style>